













	export default {
		data() {
			return {
				mv_urls:[],
			
			};
		},
		 onLoad:function(){
			
		     uni.request({
				url: 'http://106.13.79.84:8080/getVideo',
				method: 'GET',
				data: {},
				success: res => {
					
					this.mv_urls=res.data;
				
					console.log(res.data[0].img_url);
				},
				fail: () => {},
				complete: () => {}
			}); 
			 // uni.startPullDownRefresh();
		},
		 onReachBottom:function(){
			uni.request({
				url: 'http://106.13.79.84:8080/getVideo',
				method: 'GET',
				data: {},
				success: res => {
					
					this.mv_urls=this.mv_urls.concat(res.data);
					
				},
				fail: () => {},
				complete: () => {}
			});
		},
		onPullDownRefresh:function() {
		  uni.request({
			url: 'http://106.13.79.84:8080/getVideo',
			method: 'GET',
			data: {},
			success: res => {
				this.mv_urls=res.data;
				 setTimeout(function () {
				    uni.stopPullDownRefresh();
				}, 1000);
			},
			fail: () => {},
			complete: () => {}
		});
       
    }
	}
